<template>
  <div>
    <div class="card1 col-12">
      <card>
        <div class="row col-12" style="padding-top: 5px">
          <p class="ml-3" style="font-size: 19px">:</p>
          <div class="col-4">
            <slider
              vertical
              class="mt-3"
              type="info"
              value="number"
              :options="{ step: 1 }"
              v-model="tmpvalue1"
              :range="{ min: 0, max: 1000 }"
            ></slider>

            <div class="mt-3">{{ sliderValue1 }}</div>
          </div>
          <p style="margin-left: 29px; font-size: 19px">:</p>
          <div class="col-4">
            <slider
              class="mt-3"
              type="info"
              value="number"
              :options="{ step: 1 }"
              v-model="tmpvalue2"
              :range="{ min: 0, max: 1000 }"
            ></slider>

            <div class="mt-3">{{ sliderValue2 }}</div>
          </div>
          <div>
            <base-button
              type="info"
              class="animation-on-hover"
              @click.prevent="submit"
              style="width: 90px; height: 35px"
              ><h4
                style="
                  margin-top: 10px;
                  writing-mode: vertical-lr;
                  color: #fffffb;
                  justify-content: center;
                "
              >
                
              </h4></base-button
            >
            <base-button
              type="info"
              class="animation-on-hover ml-3"
              @click="routerToModel"
              style="width: 90px; height: 35px"
              ><h4
                style="
                  margin-top: 10px;
                  writing-mode: vertical-lr;
                  color: #fffffb;
                  justify-content: center;
                "
              >
                
              </h4></base-button
            >
          </div>
        </div>
      </card>
    </div>
    <div class="card2">
      <div class="row">
        <card class="photo ml-5">
          <img v-bind:src="pathOrigin" />
          <p class="card-text" style="font-size: 20px; text-align: center">
            
          </p>
        </card>
        <card class="photo ml-3">
          <img v-bind:src="pathAfter" />
          <p class="card-text" style="font-size: 20px; text-align: center">
            
          </p>
        </card>
      </div>
    </div>
    <div class="photoSlider">
      <slider
        class="mt-3"
        type="info"
        value="number"
        :options="{ step: 1 }"
        v-model="photoSliderValue"
        :range="{ min: this.photoSliderMin, max: this.photoSliderMax }"
      >
      </slider>
      <div class="photoword mt-3">
        <h3>{{ SliderValue3 }}</h3>
      </div>
    </div>
  </div>
</template>

<script>
import request from "../util/request";
import BaseButton from "../components/BaseButton.vue";
import Slider from "../components/Slider.vue";
import Swal from "sweetalert2";
export default {
  components: {
    BaseButton,
    Slider,
  },
  data() {
    return {
      requestBody: {},
      modelState: false,
      photoNum: "",
      photoPath: "",
      window_level: "",
      window_width: "",
      tmpvalue1: "280",
      tmpvalue2: "400",
      photoSliderValue: 1,
      photoSliderMax: 2,
      photoSliderMin: 1,
      folderName: "",
      testNumber: 123,
    };
  },
  computed: {
    sliderValue1() {
      let ans = Math.trunc(this.tmpvalue1);
      return ans;
    },
    sliderValue2() {
      let ans = Math.trunc(this.tmpvalue2);
      return ans;
    },
    pathOrigin() {
      const thepath =
        "/static/jpgs/" +
        this.folderName +
        "/origin_" +
        this.SliderValue3 +
        ".jpg";
      return thepath;
    },
    pathAfter() {
      const thepath =
        "/static/jpgs/" +
        this.folderName +
        "/after_" +
        this.SliderValue3 +
        ".jpg";

      return thepath;
    },
    SliderValue3() {
      let ans = Math.trunc(this.photoSliderValue);
      return ans;
    },
  },
  methods: {
    submit() {
      this.requestBody.window_level = this.sliderValue1;
      this.requestBody.window_width = this.sliderValue2;
      this.requestBody.offset = 0;
      (this.requestBody.folderName = sessionStorage.getItem("folderName")),
        (this.requestBody.dicom_folder =
          sessionStorage.getItem("dicom_folder"));
      console.log(this.requestBody);

      request
        .post("http://localhost:5000/confirm", this.requestBody)
        .then((res) => {
          sessionStorage.setItem("objName", res.obj_name);
          sessionStorage.setItem("size", res.size);
          sessionStorage.setItem("savePath", res.savePath);
          console.log(res);
          Swal.fire({
            title: "",
            icon: "success",
          });
        });
    },
    routerToModel() {
      let body = {
        savePath: sessionStorage.getItem("savePath"),
        size: sessionStorage.getItem("size"),
        folderName: this.folderName,
      };
      console.log(body)
      request.post("http://localhost:5000/generateModel",body).then(res=>{
        console.log(res)
      })
      // if (this.modelState) {
      //   this.$router.push("model");
      // }else{
      //   Swal.fire({
      //     title:"",
      //     icon:"error"
      //   })
      // }
    },
  },
  created() {
    this.photoNum = sessionStorage.getItem("photoNum");
    this.folderName = sessionStorage.getItem("folderName");
    this.photoSliderMax = parseInt(this.photoNum);
  },
  mounted() {},
};
</script>

