<template>
  <div>
    <card class="uploadcard">
      <div class="cardtop">
        <Upload
          action="http://127.0.0.1:5000/load"
          accept=".zip , .tar"
          :before-upload="beforeUploadFile"
          :on-success="handleSuccess"
          :on-error="handleerror"
          :on-change="handlechange"
          :limit="1"
        >
          <base-button
            size="lg"
            type="info"
            class="mb-3"
          >
            <i class="tim-icons icon-cloud-upload-94"></i></base-button
          >
          <!--  -->
          <h5>.zip.tar500MB</h5>
        </Upload>
      </div>
    </card>
  </div>
</template>

<script>
import { Upload } from "element-ui";
import BaseButton from "../components/BaseButton.vue";
import swal from "sweetalert2";
import Card from "../components/Cards/Card.vue";
export default {
  components: {
    Upload,
    BaseButton,
    Card,
  },
  methods: {
    handleSuccess(res, file, fileList) {
      sessionStorage.setItem("photoNum",res.num)
      sessionStorage.setItem("folderName",res.folder_name)
      sessionStorage.setItem("dicom_folder",res.dicom_folder)
      swal.fire({
        title: "",
        icon: "success",
        confirmButtonText: "",
      }).then(res=>{
        if(res.isConfirmed){
          this.$router.push('photo')
        }
      });
    },
    handleerror(res, file, fileList) {
      swal.fire({
        title: "",
        icon: "error",
        confirmButtonText: "",
      });
    },
   handlechange(){
      console.log("change test")
    },
    beforeUploadFile(file) {
      //
      const isRightType =
        file.name.endsWith(".zip") || file.name.endsWith("tar");

      //
      const isLt2M = file.size / 1024 / 1024 < 500;

      //
      if (!isRightType) {
        swal.fire({
          title: "",
          text: ".zip.tar",
          icon: "error",
          confirmButtonText: "  ",
        });
      }
      if (!isLt2M) {
        swal.fire({
          title: "",
          text: "500MB",
          icon: "error",
          confirmButtonText: "",
        });
      }
      return isRightType && isLt2M;
    },
   
   
  },
  mounted() {
   
  },
};
</script>

<style>
.uploadcard {
  background: rgba(235, 238, 241, 0.1);
  border-radius: 15px;
  box-sizing: border-box;
  width: 500px;
  height: 280px;
  text-align: center;
  position: relative;
  margin: 320px auto;
}
.cardtop {
  margin-top: 80px;
}
</style>
